import { Epic, Tabbar, TabbarItem, View, Panel, ModalRoot, ModalPage,  Div, Checkbox, Title, FormLayout, Switch, Cell, ScreenSpinner, Button, ModalPageHeader, Card, ConfigProvider, Spinner, Avatar, InfoRow, FormStatus, SimpleCell, IS_PLATFORM_IOS , IS_PLATFORM_ANDROID , PanelHeaderButton } from '@vkontakte/vkui'
import bridge from '@vkontakte/vk-bridge'
import axios from 'axios';



// icons
import Icon28Users3Outline from '@vkontakte/icons/dist/28/users_3_outline';
import Icon28Settings from '@vkontakte/icons/dist/28/settings';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28Users from '@vkontakte/icons/dist/28/users';
import Icon28Send from '@vkontakte/icons/dist/28/send';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';
import Icon28ServicesOutline from '@vkontakte/icons/dist/28/services_outline';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';


// panels
import Groups from './panels/Groups'
import GroupInfo from './panels/GroupInfo';
import Catalog from './panels/Catalog'
import SectionCatalog from './panels/SectionCatalog'
import MySubs from './panels/MySubs'
import Friends from './panels/Friends'
import Settings from './panels/Settings'
import EditSettings from './panels/EditSettings';
import ChooseCountry from './components/ChooseCountry';
import ChooseCity from './components/ChooseCity';
import ChooseCountryByContinent from './components/ChooseCountryByContinent';
import SnackbarError from './components/SnackbarError';

const blue = {
  color:'var(--accent)'
}

const SEASONS_TRANSCRIPTION = {
  'all': 'любое',
  'winter': 'зима',
  'spring': 'весна',
  'summer': 'лето',
  'fall': 'осень',
};
