import React, { Component } from 'react'
import { Panel, PanelHeader, FixedLayout, Tabs, TabsItem, Div, Button } from '@vkontakte/vkui'
import MyGroups from '../components/MyGroups'
import MyDB from '../components/MyDB'

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';

export class MySubs extends Component {
  state = {
    activeTab: 'groups',
  }

  render() {
    const {
      activeTab
    } = this.state
    const {
      id,
      state
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader>Подписки</PanelHeader>
        <FixedLayout vertical="top">
          <Tabs>
            <TabsItem
              selected={activeTab === 'groups'}
              onClick={() => this.setState({ activeTab: 'groups' })}
            >Витрина</TabsItem>
            <TabsItem
              selected={activeTab === 'database'}
              onClick={() => this.setState({ activeTab: 'database' })}
            >Мой выбор</TabsItem>
          </Tabs>
        </FixedLayout>
        <Div style={{ marginTop: 40, paddingBottom: 0 }}>
        <Button mode='secondary' size='xl' before={<Icon28AddOutline />} onClick={() => this.props.onChangeGroups('activePanel', (activeTab === 'database' ? 'settings' : 'groups'))}>Добавить</Button>
        </Div>
        {
          activeTab === 'groups' ? 
            <MyGroups
              setPopout={this.props.setPopout}
              onChangeGroups={this.props.onChangeGroups}
              openModal={this.props.openModal}
              state={state}
            />
          :
            <MyDB
              onChangeGroups={this.props.onChangeGroups}
              setPopout={this.props.setPopout}
              state={state}
              onChange={this.props.onChange}
              go={this.props.go}
            />
        }
      </Panel>
    )
  }
}

export default MySubs
