import React, { Component } from 'react'
import { render } from '@testing-library/react';
import { Snackbar } from '@vkontakte/vkui';

import Icon24Error from '@vkontakte/icons/dist/24/error';

export class SnakbarError extends Component {
    state = {
        
    }

    componentDidMount() {
        console.log(this.props.text);
    }

    render() {
        return(
        <Snackbar onClose={this.props.close} before={<Icon24Error fill='#FA5728'/>}>{this.props.text ? this.props.text : "Ошибка интернет-соеденения."}</Snackbar>
        )
    }
}
 
export default SnakbarError;